<template>
  <div class="page page-msg_detail">
    <div class="content">
      {{ content }}
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      content: "",
    };
  },
  created() {
    this.content = this.$st.get("MSG_CONTENT");
    this.$root.$emit("root:set-topbar", ["left-back"]);
  },
  beforeDestroy() {
    this.$st.remove("MSG_CONTENT");
  },
};
</script>

<style lang="scss">
.page.page-msg_detail {
  .content {
    padding: 15px;
  }
}
</style>
